import { turn } from 'core-js/fn/array'
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    pageBlock: true,
    token:null,
    loading:false,
    loaded: false,
    addAnim:false,
    scroll:0,
    changePageBlack: false,
    pageDefault:{
      webStart:false,
      slideIn:false,
    }
  },
  mutations: {
    /*  */
    setPageBlock(state,data){
      state.pageBlock = data
    },
    login: (state,data)=> {
      localStorage.token=data
      state.token=data
    },
    /*  */
    setLoading(state){      
      setTimeout(() => {
        state.loading = true
      },300)
    },
    setLoadIngNext(state){
      setTimeout(() => {
        state.loading = false
      },1500)
    },
    setLoaded(state){
      setTimeout(() => {
        state.loaded = true
        state.changePageBlack = false
      },1000)
    },
    setAddAnim(state){
      setTimeout(() => {
        state.addAnim = true
      },1000)
    },
    setScrollAction(state){
      window.addEventListener('scroll', () => {
      let scrollTop = document.documentElement.scrollTop ||
                      document.body.scrollTop ||
                      document.querySelector('.element').scrollTop;
        state.scroll = scrollTop
        // console.log(state.scroll);
      }, true);
    },
    /* router  */    
    gatResetState(state){
      state.loading = false
      state.loaded =  false
      state.addAnim = false
      state.changePageBlack = true
      // web
      state.pageDefault.webStart = false
      state.pageDefault.slideIn = false
    },
    /*  */
    setloadIngOk(state){
      setTimeout(() => {
        state.pageDefault.webStart = true
        state.pageDefault.slideIn = true
      },2300)
    }
  },
  actions: {
    /*  */
    pageBlock({commit}){
      commit('setPageBlock');
    },
    resetState({commit}){
      commit('gatResetState');
    },
    loadIng({commit}){
      commit('setLoading');
    },
    loadIngNext({commit}){
      commit('setLoadIngNext');
    },
    loaded({commit}){
      commit('setLoaded');
    },
    addAnim({commit}){
      commit('setAddAnim')
    },
    scrollAction({commit}){
      commit('setScrollAction');
    },
    loadIngOk({commit}){
      commit('setloadIngOk');
    },
  },
  modules: {
  }
})
